@import "variables";
@import "functions";
@import "extends";
@import "mixins";

/* main */
.main{
	&, &__full-page{
		padding-top: 65px;
	}
	&__full-page{
		@extend %fwh;
	}
}

/* first screen */
.first-screen{
	overflow: hidden;
}

/* section */
.section{
	padding-top: 30px;
	padding-bottom: 30px;
}

/* list */
.list{
	list-style: none;
	li{
		padding-left: 30px;
		margin-top: 30px;
		@include fch(t);
		position: relative;
		&:before{
			@extend %ab;
			position: absolute;
		}
	}
}
ul.list{
	li{
		&:before{
			width: 9px;
			height: 9px;
			left: 3px;
			top: 6px;
			background: $rd;
		}
	}
}
ol.list{
	counter-reset: listCounter;
	li{
		&:before{
			counter-increment: listCounter;
			content: counter(listCounter)".";
			color: $be;
			font-weight: 700;
			top: 0;
			left: 0;
		}
	}
}

/* image */
.fullwidth-img{
	width: 100%;
	height: auto;
}

/* title */
.title{
	font-size: 26px;
	font-weight: 700;
	color: $gn;
	margin-bottom: em(25,26);
}

/* article */
.article{
	h2{
		font-size: 22px;
		font-weight: 700;
		line-height: 1.2;
		margin: 45px 0 30px;
		@include fch(t);
	}
	p{
		line-height: 1.35;
		margin-top: 25px;
		@include fch(t);
	}
	blockquote{
		line-height: 1.35;
		margin-top: 35px;
		@include fch(t);
	}
}

/* work */
.work{
	padding-bottom: 35px;
	margin-bottom: 35px;
	position: relative;
	&__thumb{
		border: 2px solid $gy;
		img{
			width: 100%;
			height: auto;
		}
	}
	&__info{
		padding: 15px 0 0 15px;
	}
	&:before{
		@extend %ab;
		position: absolute;
		bottom: 0;
		left: 10px;
		right: 10px;
		height: 1px;
		background: $gy;
	}
	&:last-child{
		margin-bottom: 0;
		padding-bottom: 0;
		&:before{
			display: none;
		}
	}
	&__title{
		font-size: 18px;
		font-weight: 500;
		margin-bottom: 5px;
	}
	&__desc{
		font-size: 14px;
		line-height: 1.3;
		margin-bottom: 25px;
		color: $dgy;
	}
	&__techn{
		margin-bottom: 25px;
	}
	&__icon{
		margin: 0 10px;
		vertical-align: middle;
	}
	&__date{
		font-size: 14px;
		color: $dgy;
		margin-top: 15px;
		position: relative;
		padding-left: 22px;
		opacity: .75;
		&:before{
			@extend %ab;
			width: 15px;
			height: 14px;
			// @extend %svg-share-square;
			// @include bgs(576);
			position: absolute;
			left: 0;
			top: 50%;
			margin-top: -8px;
		}
	}
	@media(max-width: 1000px){
		&__info{
			padding: 0;
		}
		&__icon{
			margin: 0 7px 15px;
		}
	}
}

/*person*/
.person{
	&__thumb{
		border: 2px solid $gy;
	}
	
	&__info{
		padding: 15px 15px 0 0;
	}
	
	@media(max-width: 1000px){
		&__info{
			padding: 0;
			margin-bottom: 15px;
		}
		
	}
}

/*skills*/
.skills{
	&__icon{
		margin: 0 7px 15px;
		vertical-align: middle;
	}
}

